import Vue from 'vue'
import Router from 'vue-router'
import BlogLogin from '@/components/manage/BlogLogin.vue'
import BlogIndex from '@/components/home/BlogIndex.vue'
import SearchFile from '@/components/home/SearchFile.vue'
import CreateAccount from '@/components/home/CreateAccount.vue'
import ExistAccount from '@/components/home/ExistAccount.vue'
import ListShow from '@/components/accountlist/ListShow.vue'
import Table from '@/components/table/Table.vue'
import test from '@/components/home/test.vue'


Vue.use(Router)

export default new Router({
  linkActiveClass:'active',
  routes: [
    {
      path: '/',
      name: 'BlogLogin',
      component: BlogLogin
    }, 
     {
      path: '/index',
      name: 'BlogIndex',
      component: BlogIndex
    },
    {
      path: '/create',
      name: 'CreateAccount',
      component: CreateAccount
    },
    {
      path: '/exist',
      name: 'ExistAccount',
      component: ExistAccount
    },
    {
      path: '/search',
      name: 'SearchFile',
      component: SearchFile
    },
    {
      path: '/list',
      name: 'ListShow',
      component: ListShow
    },
    {
      path: '/table',
      name: 'Table',
      component: Table
    },
    {
      path: '/test',
      name: 'test',
      component: test
    }
  ]
})



