<template> 
  <div class="page-component_scroll">
        <el-backtop target=".page-component_scroll" :right="120" :bottom="50" >
        </el-backtop> 
    <el-container>
      <el-header>
            <div class="nav-left">
              <img src="@/assets/image/boc2013_logo.png" alt="图片加载失败">
            </div>
            <div class="nav-center">
               <ul>
                    <li v-for="(list, index) in headerList" :key="list.id">  
                     <router-link class="nav-item" :to="{name:list.name}">
                      {{ list.title }}
                     </router-link>
                    </li>
               </ul>
            </div>
            <div class="nav-right">
                <span class="nav-user">欢迎：{{username}}</span>
                <span class="nav-user">部门：{{department}}</span>
                <span class="nav-user">团队：{{team}}</span>
                <span>{{date | formatDate}}</span>
            </div>
      </el-header>
       <hr style="height:3px;" color="#a71e32"/>
      <el-main>
         <div  class="title">
             <span>已建台账</span>
         </div>
        <el-table :data="tableData" style="width: 60%" class="account-table"  stripe="true">
          <el-table-column class= "account-column" label="创建日期" align="center" prop="date">
             <template slot-scope="scope"> 
               <div class="create-data" >
                 <i class="el-icon-time"></i>
                 <span>{{ scope.row.createtime }}</span>
               </div>
             </template>  
          </el-table-column>
          <el-table-column class= "account-column" label="创建人" align="center"> 
             <template slot-scope="scope">
                <div class="create-user">
                  <el-tag size="medium">{{ scope.row.creatorname }}</el-tag>
                </div>
             </template>   
          </el-table-column>
          <el-table-column class= "account-column" label="台账名" align="center"> 
             <template slot-scope="scope">
                <div class="create-account">
                  <span>{{ scope.row.tablename}}</span>
                </div>
             </template>   
          </el-table-column>
          <el-table-column class= "account-column" label="操作" align="center">
            <template>
                <el-button size="mini" type="warning" @click="handleEdit">编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
     </el-main>
   </el-container>
  </div>
</template>

<script>
    //import blogHeader from '@/components/common/BlogHeader.vue'
    //import blogFooter from '@/components/common/BlogFooter.vue'
    import Vue from 'vue'
    var padDate = function (value) {
    return value <10 ? '0' + value:value;
    };
    export default {
        name: 'BlogIndex',
        data(){
           return{
               username:'',
               department:'',
               level:'',
               team:'',
               date:new Date(),
               tableData:[],
               officenum:'',
               headerList: [
                   {id: '1', name:'BlogIndex', title:'首页'},
                   {id: '2', name:'CreateAccount', title:'创建台账'},
                   {id: '3', name:'ExistAccount', title:'已建台账'},
                   {id: '4', name:'SearchFile', title:'查询台账'},
               ],
               isShow: false,
            //}
        //},       
               
            //test
                tableData: [{
          createtime: '2016-05-02',
          creatorname: '王小虎',
          tablename: '修炼法则'
        }, {
          createtime: '2016-05-04',
          creatorname: '李逍遥',
          tablename: '蜀山录'
        }, {
          createtime: '2016-05-01',
         creatorname: '赵灵儿',
          tablename: '女娲补天记'
        }, {
           createtime: '2016-05-03',
          creatorname: '林月如',
          tablename: '林家宝秘籍'
        },{
          createtime: '2016-05-03',
          creatorname: '雪见',
          tablename: '任性法则'
        },{
          createtime: '2016-05-03',
          creatorname: '雪见',
          tablename: '任性法则'
        },{
           createtime: '2016-05-03',
          creatorname: '雪见',
          tablename: '任性法则'
        },{
           createtime: '2016-05-03',
          creatorname: '雪见',
          tablename: '任性法则'
        },{
          createtime: '2016-05-03',
          creatorname: '雪见',
          tablename: '任性法则'
        },
        ]
              
       }
     },
    

        filters: {
            formatDate:function (value) {
                var date = new Date(value);
                var year = date.getFullYear();
                var month = padDate(date.getMonth()+1);
                var day = padDate(date.getDate());
                var hours = padDate(date.getHours());
                var minutes = padDate(date.getMinutes());
                var seconds = padDate(date.getSeconds());
                return year + '-' + month + '-' + day + '-' + ' ' + hours + ':' + minutes + ':' + seconds;
            }
        },

        mounted(){
                var _this = this;
                this.timer = setInterval(function(){
                    _this.date = new Date();
                },1000)
            },

        beforeDestroy(){
                if(this.timer){
                    clearInterval(this.timer);
                }

            },

        
        created() {
                let user = JSON.parse(localStorage.getItem('user'));
                this.username = user.username;
                this.department = user.departmentname;
                this.team = user.teamname;
                //var officenum = JSON.parse(localStorage.getItem('officenum'));
                //this.getuserinfo();
                this.gettableinfo();
            },

        methods: {
            /*
            getuserinfo() {
                var officenum = JSON.parse(localStorage.getItem('officenum')); 
                this.axios
                .post('/autosearch',{officenum:this.officenum})
                .then(response => {
                   console.log(response.data)
                   //var userinfo = JSON.parse(JSON.stringify(response.data));
                   this.username = response.data.username;
                   //this.headofficenum = response.data.headofficenum;
                   //this.officenum = response.data.officenum;
                   this.department = response.data.department;
                   this.level = response.data.level;
                   this.team = response.data.team;
                })
                .catch(error => {
                    console.log(error);
                })
            },
            */
            
            gettableinfo() {
                //var userid = JSON.parse(localStorage.getItem('userid'));
                //var userlevel = JSON.parse(localStorage.getItem('userlevel'));
                //var creatorname = JSON.parse(localStorage.getItem('username'));
               // var tablename = JSON.parse(localStorage.getItem('tablename'));
                //var StartDate = JSON.parse(localStorage.getItem('StartDate'));
                //var EndDate = JSON.parse(localStorage.getItem('EndDate'));
                this.axios
                .post('/autosearch',{
                    user:this.user
                })
                .then(response=>{
                    this.tableData = response.data.content
                })
                .catch(error=>{
                    console.log("无台账");
                })
            },

            handleEdit() {
                
            }
        }

   
    }
</script>

<style scoped>
    @import '../../assets/style/home.css';
    @import '../../assets/style/table-list.css';
</style>




