<template>
  <div>
    <div>
     <img src="@/assets/image/boc.jpg" class="logo"> 
    </div>
    <div class="page-container">
      <h3>欢迎登录</h3>
      <form>
          <el-input type="username" class="user-input" v-model="data.officenum" placeholder="请输入工号"></el-input>
          <el-button class="btn" v-on:click="login">登录</el-button>
      </form>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  name: 'BlogLogin',
  
  data () {
    return {
      data: {officenum:''},
      //userinfo:{}
    }
  },
  methods: {
    login: function () {
      if(this.data.officenum.trim() === ""){
        return alert("请输出工号");
      }
     
      //将数组序列化成字符串存入localstorage中
      localStorage.setItem('officenum',JSON.stringify(this.data.officenum));
      this.axios
         .post('/login',{officenum: this.data.officenum})
         .then(response => {
           console.log(response)
           console.log(response.data)
           localStorage.setItem('user',JSON.stringify(response.data.content));
           //var userinfo = JSON.parse(JSON.stringify(response.data));
           this.$router.push({path: '/index'});
           
         })
         .catch(error => {
                    console.log(error)
                    //console.log("用户不存在");
                })
      
    }
  }
}
</script>

<style scoped>  
   @import '../../assets/style/login.css';
</style>





