<template>
  <div class="page-component_scroll">
    <el-backtop target=".page-component_scroll" :right="80" :bottom="50" visibility-height=100>
    </el-backtop>  
    <el-container>
      <el-header>
            <div class="nav-left">
              <img src="@/assets/image/boc2013_logo.png" alt="图片加载失败">
            </div>
            <div class="nav-center">
               <ul>
                    <li v-for="(list, index) in headerList" :key="list.id">  
                     <router-link class="nav-item" :to="{name:list.name}">
                      {{ list.title }}
                     </router-link>
                    </li>
               </ul>
            </div>
            <div class="nav-right">
                <span class="nav-user">欢迎：{{username}}</span>
                <span class="nav-user">部门：{{department}}</span>
                <span class="nav-user">团队：{{team}}</span>
                <span class="nav-user">职位：{{level}}</span>
                <span>{{date | formatDate}}</span>
            </div>
      </el-header>
       <hr style="height:3px;" color="#a71e32"/>
      <el-main>
         <el-form :model="tableForm" label-width="130px" size="small" width="600px">
                <el-form-item label="台账名" prop="tablename" class="tabletitle" label-width="160px" 
                  :rules="[
                  { required: true, message: '台账名不能为空', trigger: 'blur' },
                   ]"
                >
                   <el-input size="medium" class="tablename" v-model="tableForm.tablename" placeholder="请输出台账名"></el-input>                  
                </el-form-item>  
                <el-form-item class="create">
                   <el-button class="btn-add" type="primary" @click="createRow(infiledList)">新增</el-button>
                </el-form-item>
               <template>
                 <el-table border :data="infiledList" class="table">
                      <el-table-column  prop="fildna" label="名称" style="width:6vw;" align="center" >
                        <template slot-scope="scope">
                           <el-input size="medium" v-model="scope.row.fildna" placeholder="请输出名称"></el-input>
                       </template>
                       </el-table-column>
                      <el-table-column  prop="fildtp" label="类型" align="center">
                      <template slot-scope="scope">
                            <el-select v-model="scope.row.fildtp" clearable >
                              <el-option
                                v-for="item in fildtps"
                                :key="item.value"
                                :label="item.text"
                                :value="item.value">
                              </el-option>
                            </el-select>
                        </template>
                      </el-table-column>
                     <el-table-column fixed="right"  label="操作" align="center">
                       <template slot-scope="scope">
                         <el-button type="success"  size="small" icon="el-icon-circle-plus" @click="addRow(infiledList)"></el-button>
                         <el-button type="danger" size="small" icon="el-icon-delete" @click.native.prevent="deleteRow(scope.$index, infiledList)"></el-button>
                       </template>
                     </el-table-column>
                 </el-table>
               </template>
               <el-form-item>
               <el-button class="create-btn" type="primary" size="medium" @click="createTable">立即创建</el-button>
    
               </el-form-item>
        </el-form> 
    </el-main>  
   </el-container>
  </div> 
</template>

<script>
    //import blogHeader from '@/components/common/BlogHeader.vue'
    //import blogFooter from '@/components/common/BlogFooter.vue'
    import Vue from 'vue'
    var padDate = function (value) {
    return value <10 ? '0' + value:value;
    };
    export default {
        name: 'BlogIndex',
        data(){
           return{
               username:'',
               department:'',
               level:'',
               team:'',
               tablename:'',
               infiledList:[],
               fildtps:[{text:'文字',value:'1'},{text:'整数',value:'2'},{text:'小数',value:'3'},{text:'日期',value:'4'}],
               date:new Date(),
               officenum:'',
               headerList: [
                   {id: '1', name:'BlogIndex', title:'首页'},
                   {id: '2', name:'CreateAccount', title:'创建台账'},
                   {id: '3', name:'ExistAccount', title:'已建台账'},
                   {id: '4', name:'SearchFile', title:'查询台账'},
               ],
               isShow: false,
               tableForm: {
                   tablename:''
               }         
             
           }
        },


        filters: {
            formatDate:function (value) {
                var date = new Date(value);
                var year = date.getFullYear();
                var month = padDate(date.getMonth()+1);
                var day = padDate(date.getDate());
                var hours = padDate(date.getHours());
                var minutes = padDate(date.getMinutes());
                var seconds = padDate(date.getSeconds());
                return year + '-' + month + '-' + day + '-' + ' ' + hours + ':' + minutes + ':' + seconds;
            }
        },

        mounted(){
                var _this = this;
                this.timer = setInterval(function(){
                    _this.date = new Date();
                },1000)
            },

        beforeDestroy(){
                if(this.timer){
                    clearInterval(this.timer);
                }

            },

        
        created() {
                let user = JSON.parse(localStorage.getItem('user'));
                let tablemeta = JSON.parse(localStorage.getItem('tablemeta'));
                this.username = user.username;
                this.department = user.departmentname;
                this.team = user.teamname;
                //var officenum = JSON.parse(localStorage.getItem('officenum'));
                //this.getuserinfo();
            },

        methods: {
            
            deleteRow(index, rows) {
                rows.splice(index, 1);
              },

            createRow(tableData,event){
                tableData.push({ fildna: '',fildtp:'' })
             },
            
           addRow(index, tableData) {
               tableData.splice(index,0,{fildna:'',fildtp:''})
           }, 
            
            createTable() {
                this.axios
                .post('')
            }

        }

   
    }
</script>

<style scoped>
    @import '../../assets/style/home.css';
    .tabletitle{
       float: left;
       width: 500px;

    }
    .title{
        font-family: "PingFang SC";
        font-size: 20px;
    }

    .create{
        float: left;
        margin-left: 514px
    }

    .table{
        
         margin: 0 auto; 
         margin-left: 100px;
         width: 85%;
    }
    .page-component_scroll {
    height: 85vh;
    overflow-x: hidden;
    }
    .create-btn{
        margin-top: 20px;
        margin-right: 140px;
    }
    
</style>




