<template>  
  <el-container>
      <el-header>
            <div class="nav-left">
              <img src="@/assets/image/boc2013_logo.png" alt="图片加载失败">
            </div>
            <div class="nav-center">
               <ul>
                    <li v-for="(list, index) in headerList" :key="list.id">  
                     <router-link class="nav-item" :to="{name:list.name}">
                      {{ list.title }}
                     </router-link>
                    </li>
               </ul>
            </div>
            <div class="nav-right">
                <span class="nav-user">欢迎：{{username}}</span>
                <span class="nav-user">部门：{{department}}</span>
                <span class="nav-user">团队：{{team}}</span>
                <span>{{date | formatDate}}</span>
            </div>
      </el-header>
       <hr style="height:3px;" color="#a71e32"/>
      <el-main>
            <div class="user-search">
                <span class="search-text">搜索创建人： </span>
                <el-input placeholder="请输入创建人" class="user-input"  v-model="file.username">
                </el-input>
            </div>
            <div class="file-search">
                <span class="search-text">搜索台账名： </span>
                <el-input placeholder="请输入台账名" class="file-input"  v-model="file.filename">
                </el-input>    
            </div>    
            <div class="date-search">
               <span class="search-text">搜索日期： </span>
               <el-date-picker
                  class="date-input"
                  v-model="datevalue"
                  type="daterange"
                  align="right"
                  unlink-panels
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                >
                </el-date-picker>
            </div>
            <div class="search-button">
                <el-button class="btn-s" v-on:click= "search" type="primary" icon="el-icon-search">搜索</el-button>
            </div>
      </el-main>
   </el-container>
</template>

<script>
    //import blogHeader from '@/components/common/BlogHeader.vue'
    //import blogFooter from '@/components/common/BlogFooter.vue'
    import Vue from 'vue'
    var padDate = function (value) {
    return value <10 ? '0' + value:value;
    };
    export default {
        name: 'BlogIndex',
        data(){
           return{
               userid:'',
               userlevel:'',
               username:'',
               department:'',
               level:'',
               team:'',
               file:{uesrname:'', filename:''},
               datevalue:[],
               date:new Date(),
               officenum:'',
               headerList: [
                   {id: '1', name:'BlogIndex', title:'首页'},
                   {id: '2', name:'CreateAccount', title:'创建台账'},
                   {id: '3', name:'ExistAccount', title:'已建台账'},
                   {id: '4', name:'SearchFile', title:'查询台账'},
               ],
               isShow: false
           }
        },

        filters: {
            formatDate:function (value) {
                var date = new Date(value);
                var year = date.getFullYear();
                var month = padDate(date.getMonth()+1);
                var day = padDate(date.getDate());
                var hours = padDate(date.getHours());
                var minutes = padDate(date.getMinutes());
                var seconds = padDate(date.getSeconds());
                return year + '-' + month + '-' + day + '-' + ' ' + hours + ':' + minutes + ':' + seconds;
            }
        },

        mounted(){
                var _this = this;
                this.timer = setInterval(function(){
                    _this.date = new Date();
                },1000)
            },

        beforeDestroy(){
                if(this.timer){
                    clearInterval(this.timer);
                }

            },

        
        created() {
                //var officenum = JSON.parse(localStorage.getItem('officenum'));
                //this.getuserinfo();
                let user = JSON.parse(localStorage.getItem('user'));
                this.username = user.username;
                this.department = user.departmentname;
                this.team = user.teamname;
                this.userid = user.userid;
                this.userlevel = user.level;
            },
                
        methods: {
            /*
            getuserinfo() {
                var officenum = JSON.parse(localStorage.getItem('officenum')); 
                this.axios
                .post('/autosearch',{officenum:this.officenum})
                .then(response => {
                   console.log(response.data)
                   this.username = response.data.username;
                   this.department = response.data.department;
                   this.level = response.data.level;
                   this.team = response.data.team;
                   this.userid = response.data.userid;
                   this.userlevel = response.data.userlevel
                   //localStorage.setItem('userid',JSON.stringify(response.data.userid));
                   //localStorage.setItem('userlevel',JSON.stringify(response.data.level));
                })
                .catch(error => {
                    console.log(error);
                })
            },
            */
            
            search() {

                if (this.file.username === ""){
                    this.file.username = null;
                }
                if (this.file.filename === ""){
                    this.file.filename = null;
                }
                localStorage.setItem('username',JSON.stringify(this.file.username));
                localStorage.setItem('tablename',JSON.stringify(this.file.filename));
                localStorage.setItem('startdate',JSON.stringify(this.datevalue[0]));
                localStorage.setItem('enddate',JSON.stringify(this.datevalue[1])); 
                this.axios
                .post('/search',{
                    userid:this.userid,
                    userlevel:this.userlevel,
                    creatorname:this.file.username,
                    tablename:this.file.filename,
                    startdate:this.datevalue[0],
                    enddate:this.datevalue[1]
                 })
                .then(response => {
                    console.log(response.data)
                    //this.tablemeta = response.data.content
                    localStorage.setItem('tablemeta',JSON.stringify(response.data.content))
                    this.$router.push({path: '/list'})
                })
                .catch(error => {
                    console.log("台账不存在");
                })
            }

        }

   
    }
</script>

<style scoped>
    @import '../../assets/style/home.css';
    @import '../../assets/style/search.css';
</style>





