<template>
  <div>
      <span>{{records}}</span>
      <el-button @click="submit">Test</el-button>
  </div>     
</template>

<script>
export default {
    name:"test",
    data() {
        return {
            records:{
                permission:{userid:'123', tableid:'24', level:3},
                metadata:{a:'apple', b:'orange'},
                data:[{a:'xxx', b:'xxx'}, {a:'yyy', b:'yyy'}]
            }
        }
    },
    methods: {
      submit(){
        this.axios
        .post('/addrecords',this.records)
        .then(response=>{
            console.log(response)
            console.log(response.data)
        })
        .catch(error=>{
            console.log(error)
        })
      }
    }  
    
}
</script>




